const paginationWrapper = document.querySelectorAll('.pagination__wrapper').forEach((paginationWrapper) => { 

    let thisPage = 1;

    let limit = 12;

    let list = paginationWrapper.querySelectorAll('.pagination__card');
    // const pagination = document.querySelector('.pagination__list')



    // const paginationItem = pagination.querySelectorAll('.pagination__item')

    // paginationItem.forEach((item,index) => {
    //     item.dataset.index = index;
    //     paginationItem[0].setAttribute('data-active','')
        
    //     item.classList.remove('active-page')
    //     const currentPage = document.querySelector('[data-active]')
    //     currentPage.classList.add('active-page')
    //     console.log(currentPage)

    //     item.addEventListener('click', () => {
    //         console.log('PROVERKA')
    //         console.log(thisPage)
    //         thisPage = thisPage + 1 
    //     })
    // })






    list.forEach((item,index) => {
        item.dataset.index = index;
    })

    function loadItem () {

        let beginGet = limit * (thisPage-1);
        let endGet = limit * thisPage -1;

        list.forEach((item,key,index) => {
            if(item.dataset.index >= beginGet && item.dataset.index <= endGet) {
                item.style.display = 'block';   
            } else {
                item.style.display = 'none';
            }
            
        });
        listPage ()
    };


    loadItem()

    function listPage () {
        let count = Math.ceil(list.length/limit)
        console.log(count)
        paginationWrapper.querySelector('.pagination__list ').innerHTML="";
        for (i=1;i<=count;i++) {
            let newPage = document.createElement('li');
            newPage.classList.add('pagination__item')
            newPage.innerHTML = i;
            if (i === thisPage) {
                newPage.classList.add('active-page');
            }
            newPage.setAttribute("onclick", 'changePage('+i+')');
            
            paginationWrapper.querySelector('.pagination__list ').appendChild(newPage)
            
        }
    };


    function changePage(i) {
        thisPage = i;
        loadItem();

    }
})
